import axios from "axios";
import { useContext, useState } from "react";
import { useAlert } from "react-alert";
import config from "../../../config/config";
import { GlobalContext } from "../../../context/GlobalContext";
import IconPlusItem from "../icons/IconPlusItem";
import BtnCloseModal from "./BtnCloseModal";
import BtnModalAdd from "./BtnModalAdd";
import BtnModalCancel from "./BtnModalCancel";
import InputComp from "./InputComp";
import InputImgComp from "./InputImgComp";
import SelectComp from "./SelectComp";
import TextAreaComp from "./TextAreaComp";

const FormOffer = () => {
  const alert = useAlert();
  const contextData = useContext(GlobalContext);
  const [formData, setFormData] = useState(
    contextData.modal.mode === "create"
      ? {
          name: "",
          startDate: "",
          endDate: "",
          description: "",
          discountAmount: "",
          limit: "",
          status: "",
          photo: "",
        }
      : contextData.modal.data
  );

  const [errors, setError] = useState({});
  const handleFormData = (key, value) => {
    let tempData = { ...formData };

    tempData[key] = value;

    setFormData(tempData);

    if (errors[key]) {
      let tempErrors = { ...errors };
      tempErrors[key] = "";
      setError(tempErrors);
    }
  };
  const hasError = () => {
    let error = {};
    for (let [key, value] of Object.entries({ ...formData })) {
      if (!value) {
        error[key] = `${key} is required`;
      }
    }
    console.log(error);
    if (Object.keys(error).length > 0) {
      setError({ ...error });
      return true;
    } else {
      return false;
    }
  };
  const formSubmitHandler = (e) => {
    e.preventDefault();

    if (hasError()) {
      console.log(errors);
      return;
    } else {
      if (contextData.modal.mode === "create") {
        axios
          .post(
            `${config.SERVER_URL}/api/admin/offers`,
            formData,
            config.headers
          )
          .then((res) => {
            contextData.handleModal();
            contextData.modal.reload(Math.random());
            alert.success(res.data.message);
          })
          .catch((error) => {
            if (error.response.status === 422) {
              let getErrors = {};
              Object.entries(error.response.data.errors).forEach(
                ([key, value]) => {
                  getErrors[key] = value.msg;
                }
              );
              setError(getErrors);
              console.log(error.response.data.errors);
            } else {
              alert.error("Something went wrong! Please try again.");
            }
          });
      } else {
        axios
          .put(
            `${config.SERVER_URL}/api/admin/offers/${formData._id}`,
            formData,
            config.headers
          )
          .then((res) => {
            contextData.handleModal();
            contextData.modal.reload(Math.random());
            alert.success(res.data.message);
          })
          .catch((error) => {
            if (error.response.status === 422) {
              let getErrors = {};
              Object.entries(error.response.data.errors).forEach(
                ([key, value]) => {
                  getErrors[key] = value.msg;
                }
              );
              setError(getErrors);
              console.log(error.response.data.errors);
            } else {
              alert.error("Something went wrong! Please try again.");
            }
          });
      }
    }
  };
  return (
    <div className="mx-auto w-[34rem]">
      <div className="relative py-5 px-5 md:px-10 bg-white shadow-md rounded border border-gray-400">
        <div className="w-full flex justify-start items-center space-x-2 text-gray-600 mb-3">
          <IconPlusItem />
          <span className="text-2xl font-bold">
            {contextData.modal.mode === "create" ? "New Offer" : "Update Offer"}
          </span>
        </div>
        {/* <h1 className="text-gray-800 font-lg font-bold tracking-normal leading-tight mb-4">
          Enter Admin Info
        </h1> */}
        <form
          onSubmit={(e) => formSubmitHandler(e)}
          className="space-y-2 bg-slate-100 p-2 rounded"
        >
          <InputComp
            handler={handleFormData}
            errMsg={errors.name}
            label="Name"
            id="name"
            name="name"
            type="text"
            value={formData.name}
            placeholder="Enter name"
          />
          <div className="grid grid-cols-1 space-y-4">
            <div className="grid grid-cols-2 gap-2">
              <InputComp
                handler={handleFormData}
                errMsg={errors.startDate}
                label="Start Date"
                id="startDate"
                name="startDate"
                type="date"
                value={formData.startDate}
              />
              <InputComp
                handler={handleFormData}
                errMsg={errors.endDate}
                label="End Date"
                id="endDate"
                name="endDate"
                type="date"
                value={formData.endDate}
              />
            </div>

            <div className="grid grid-cols-2 gap-2">
              <InputComp
                handler={handleFormData}
                errMsg={errors.discountAmount}
                label="Discount"
                id="discountAmount"
                name="discountAmount"
                type="number"
                value={formData.discountAmount}
                placeholder="Enter discount"
              />
              <InputComp
                handler={handleFormData}
                errMsg={errors.limit}
                label="Discount Limit"
                id="limit"
                name="limit"
                type="number"
                value={formData.limit}
                placeholder="Enter discount limit"
              />
            </div>

            <TextAreaComp
              handler={handleFormData}
              errMsg={errors.description}
              label="Description"
              id="description"
              name="description"
              placeholder="Enter description"
              value={formData.description}
              rows="2"
            />
            <div className="grid grid-cols-2 gap-2">
              <InputImgComp
                handler={handleFormData}
                errMsg={errors.icon}
                label="Photo"
                id="photo"
                name="photo"
              />
              <SelectComp
                handler={handleFormData}
                errMsg={errors.status}
                label="Status"
                id="status"
                name="status"
                value={formData.status}
                options={[
                  { value: "active", name: "Active" },
                  { value: "inactive", name: "Inactive" },
                ]}
              />
            </div>
          </div>
          <div className="flex items-center justify-start w-full">
            <BtnModalAdd /> <BtnModalCancel />
          </div>
        </form>
        <BtnCloseModal />
      </div>
    </div>
  );
};

export default FormOffer;
